@font-face {
  font-family: "Oswald";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Oswald-Regular.ttf") format("truetype");
}
@font-face {
  font-family: "Oswald";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Oswald-Medium.ttf") format("truetype");
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.header__toggle-button {
  width: 24px;
  height: 16px;
  background-color: transparent;
  border: none;
  position: relative;
}
@media (min-width: 768px) {
  .header__toggle-button {
    display: none;
  }
}
.header__toggle-button--close::before {
  content: "";
  position: absolute;
  top: -1px;
  left: 0;
  width: 24px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
  transition: ease-in-out 0.15s;
}
.header__toggle-button--open::after, .header__toggle-button--open::before {
  content: "";
  position: absolute;
  top: 10px;
  left: 0;
  width: 24px;
  height: 2px;
  background-color: #000000;
  transform: rotate(-45deg);
  transition: ease-in-out 0.15s;
}
.header__toggle-button--open::before {
  transform: rotate(45deg);
}
.header__nav-wrapper {
  display: flex;
