.card {
  &__item-title {
    font-family: "Oswald", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    text-transform: uppercase;
    color: #222222;
    padding-right: 45px;
    margin-bottom: 16px;

    @media (min-width: $tablet-width) {
      font-size: 20px;
      line-height: 24px;
      text-align: center;
      padding: 0 90px;
      margin-bottom: 21px;
      margin-top: 83px;
    }

    @media (min-width: $desktop-width) {
      line-height: 21px;
      margin-top: 88px;
      padding: 0 45px 0 55px;
    }
  }

  &__wrapper {
    padding: 19px 0 25px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-areas: "img list""img list""button button";

    @media (min-width: $tablet-width) {
      display: grid;
      grid-template-rows: 200px auto;
      grid-template-columns: 1fr;
      padding: 0;
      background-image: linear-gradient(to bottom, $white 122px, #f2f2f2 80px);
      row-gap: 0;
      grid-template-areas: "img""link""list""button";
    }
  }

  &__button {
    grid-column: 1/-1;
    font-family: "Oswald", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    text-align: center;
    text-transform: uppercase;
    color: $white;
    background-color: $green-basic;
    text-decoration: none;
    padding: 10px 0;
    grid-area: button;

    &:hover {
      background-color: #5eaa2f;
    }

    &:active {
      background-color: #5eaa2f;
      color: rgb(255, 255, 255, 0.3);
    }

    @media (min-width: $tablet-width) {
      margin: 0 37px 39px 39px;
    }
  }

  &__link {
    text-decoration: none;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    margin-bottom: 21px;

    @media (min-width: $tablet-width) {
      padding: 0 36px;
      row-gap: 5px;
      margin-bottom: 33px;
    }

    @media (min-width: $desktop-width) {
      padding: 0 35px 0 45px;
      row-gap: 7px;
    }
  }

  &__title {
    font-family: "Arial", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 16px;
    color: $special_dark-grey;
    text-align: left;

    @media (min-width: $tablet-width) {
      font-size: 16px;
      border-bottom: 1px solid #cdcdcd;
    }
  }

  &__definition {
    font-family: "Arial", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 16px;
    color: $special_dark-grey;
    text-align: right;

    @media (min-width: $tablet-width) {
      font-size: 16px;
      border-bottom: 1px solid #cdcdcd;
      padding-bottom: 9px;
    }
  }

  &__image-wrapper {
    grid-area: img;
    display: flex;
    align-items: center;
    justify-content: center;
    padding-top: 7px;

    @media (min-width: $tablet-width) {
      padding: 0;
      padding-top: 126px;
    }

    &--padding {
      padding: 11px 28px;

      @media (min-width: $tablet-width) {
        padding: 0;
        padding-top: 104px;
      }
    }
  }

  &__image-item {
    display: block;

    @media (min-width: $tablet-width) {
      margin: 0 auto;
    }
  }

  &__item-description {
    grid-area: list;
  }
}
