.header {
  &__toggle-button {
    width: 24px;
    height: 16px;
    background-color: transparent;
    border: none;
    position: relative;

    @media (min-width: $tablet-width) {
      display: none;
    }
  }

  &__toggle-button--close::before {
    content: "";
    position: absolute;
    top: -1px;
    left: 0;
    width: 24px;
    height: 2px;
    background-color: #000000;
    box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
    transition: ease-in-out 0.15s;
  }

  &__toggle-button--open::after,
  &__toggle-button--open::before {
    content: "";
    position: absolute;
    top: 10px;
    left: 0;
    width: 24px;
    height: 2px;
    background-color: $black;
    transform: rotate(-45deg);
    transition: ease-in-out 0.15s;
  }

  &__toggle-button--open::before {
    transform: rotate(45deg);
  }

  &__nav-wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 320px;
    margin: 0 auto;
    padding: 10px 20px 13px 20px;

    @media (min-width: $tablet-width) {
      width: auto;
      display: block;
      margin: 0;
      padding: 0;
    }
  }

  &__nav-list--close {
    display: none;

    @media (min-width: $tablet-width) {
      display: flex;
      list-style-type: none;
      padding-top: 18px;
      padding-right: 2px;
    }

    @media (min-width: $desktop-width) {
      padding-top: 16px;
    }
  }

  &__nav-list--open {
    display: block;
    position: absolute;
    width: 100%;
    text-align: center;
    top: 65px;
    background-color: $white;
    z-index: 1;
  }

  &__wrapper {
    position: relative;
  }

  &__nav-link {
    font-family: "Oswald", sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 20px;
    text-transform: uppercase;
    color: $black;
    text-decoration: none;
    padding: 22px 0 24px 0;
    display: inline-block;
    position: relative;

    &:hover {
      opacity: 0.6;
    }

    &--current:hover::before {
      content: none;
    }

    &--second-current:hover::before {
      content: none;
    }

    &:active {
      opacity: 0.3;
    }

    &--current:active::before {
      content: none;
    }

    &--second-current:active::before {
      content: none;
    }

    @media (min-width: $tablet-width) {
      padding: 0;
      font-size: 18px;
      line-height: 24px;
      font-weight: 400;
    }

    @media (min-width: $desktop-width) {
      font-size: 20px;
      line-height: 30px;
      color: $white;
    }

    &--current::before {
      content: none;
      position: absolute;
      height: 2px;
      background-color: $green-basic;
      right: 0;
      bottom: -10px;
      left: 0;

      @media (min-width: $tablet-width) {
        content: "";
      }

      @media (min-width: $desktop-width) {
        background-color: $white;
        bottom: -8px;
      }
    }

    &--second-current::before {
      content: none;
      position: absolute;
      height: 2px;
      background-color: $green-basic;
      right: 0;
      bottom: -10px;
      left: 0;

      @media (min-width: $tablet-width) {
        content: "";
      }

      @media (min-width: $desktop-width) {
        background-color: $green-basic;
        bottom: -8px;
      }
    }

    &--second-link {
      color: $black;
    }
  }

  &__nav-item {
    border-top: 1px solid $grey;

    &:last-child {
      border-bottom: 1px solid $grey;

      @media (min-width: $tablet-width) {
        border-bottom: none;
        margin-right: 0;
      }
    }

    @media (min-width: $tablet-width) {
      border-top: none;
      margin-right: 34px;
    }
  }

  &__nav-main {
    @media (min-width: $tablet-width) {
      display: flex;
      justify-content: space-between;
      width: 768px;
      margin: 0 auto;
      padding: 24px 30px 0;
    }

    @media (min-width: $desktop-width) {
      width: 1300px;
      padding: 53px 40px 0;
    }
  }
}
