.catalog-sections {
  &__title {
    font-family: "Oswald", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 37px;
    text-transform: uppercase;
    color: $black;
    margin-bottom: 29px;
    padding: 30px 0 0 71px;
    position: relative;

    &--circle::before {
      content: "";
      position: absolute;
      width: 50px;
      height: 50px;
      border-radius: 50%;
      background-color: $green-basic;
      left: 2px;
      top: 25px;

      @media (min-width: $tablet-width) {
        width: 201px;
        height: 201px;
        top: 15px;
        left: auto;
        right: 61px;
      }

      @media (min-width: $desktop-width) {
        width: 100px;
        height: 100px;
        left: 0;
        right: 0;
        top: -30px;
      }
    }

    &--slim::after {
      content: "";
      position: absolute;
      background-image: url("../image/icons/icon__slim.svg");
      width: 36px;
      height: 50px;
      left: 9px;
      top: 21px;

      @media (min-width: $tablet-width) {
        width: 140px;
        height: 194px;
        background-image: url("../image/icons/icon-slim__tablet.svg");
        top: 4px;
        left: auto;
        right: 92px;
      }

      @media (min-width: $desktop-width) {
        width: 70px;
        height: 97px;
        background-image: url("../image/icons/icon-slim__desktop.svg");
        left: 15px;
        right: 0;
        top: -36px;
      }
    }

    &--pro::after {
      content: "";
      position: absolute;
      background-image: url("../image/icons/icon__pro.svg");
      width: 67px;
      height: 28px;
      left: -7px;
      top: 35px;
      background-repeat: no-repeat;

      @media (min-width: $tablet-width) {
        width: 268px;
        height: 112px;
        background-image: url("../image/icons/icon-pro__tablet.svg");
        top: 60px;
        left: auto;
        right: 28px;
      }

      @media (min-width: $desktop-width) {
        background-image: url("../image/icons/icon-pro__desktop.svg");
        width: 134px;
        height: 56px;
        left: -19px;
        right: 0;
        top: -10px;
      }
    }

    @media (min-width: $tablet-width) {
      font-size: 36px;
      line-height: 36px;
      padding: 0;
      margin-bottom: 27px;
    }

    @media (min-width: $desktop-width) {
      padding-left: 162px;
      margin-bottom: 74px;
    }
  }

  &__text {
    font-family: "Arial", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    color: $special_dark-grey;
    padding-bottom: 23px;
    border-bottom: 1px solid #d9d9d9;
    margin-bottom: 17px;

    @media (min-width: $tablet-width) {
      border-bottom: none;
      font-size: 16px;
      line-height: 24px;
      color: $black;
      width: 340px;
      margin-bottom: 28px;
    }

    @media (min-width: $desktop-width) {
      width: 467px;
      margin-bottom: 3px;
    }
  }

  &__link {
    font-family: "Oswald", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 16px;
    text-transform: uppercase;
    color: $black;
    display: inline-flex;
    align-items: center;
    text-decoration: none;

    &:hover::after {
      background-image: url("../image/icons/icon-arrow__hover.svg");
      width: 32px;
    }

    &:active {
      opacity: 0.3;
    }

    @media (min-width: $tablet-width) {
      font-size: 20px;
      line-height: 30px;
    }

    &::after {
      content: "";
      background-image: url("../image/icons/icon__arrow.svg");
      width: 24px;
      height: 12px;
      margin-left: 15px;
      background-repeat: no-repeat;

      @media (min-width: $tablet-width) {
        margin-left: 25px;
      }
    }
  }

  &__wrapper {
    padding: 20px 20px;
    width: 320px;
    margin: 0 auto;

    @media (min-width: $tablet-width) {
      padding: 409px 30px 49px;
      width: 768px;
    }

    @media (min-width: $desktop-width) {
      padding: 82px 40px 71px;
      width: 1300px;
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      gap: 40px 80px;
    }
  }

  &__item {
    padding: 0 20px;
    background-color: $basic_grey;
    margin-bottom: 20px;
    padding-bottom: 20px;

    &:last-child {
      margin-bottom: 0;
    }

    @media (min-width: $tablet-width) {
      margin-bottom: 30px;
      padding: 77px 0 53px 52px;
    }

    @media (min-width: $desktop-width) {
      margin-bottom: 0;
      padding: 77px 0 58px 52px;
    }
  }
}
