.promo {
  &__wrapper {
    text-align: center;
    padding: 0 20px;
    background-image: url("../image/background/promo__back.webp"), linear-gradient($dark-green 0, $dark-green 302px, $white 302px);
    background-position: top;
    background-repeat: no-repeat;
    background-color: $dark-green;

    @media (min-resolution: 2dppx) {
      background-image: url("../image/background/promo__back@2x.webp"), linear-gradient($dark-green 0, $dark-green 302px, $white 302px);
      background-size: auto 302px, auto;
    }

    @media (min-width: $tablet-width) {
      background-image: none;
      background-color: transparent;
      position: relative;
      padding: 0 120px 117px;
      width: 768px;
      margin: 0 auto;
    }

    @media (min-width: $desktop-width) {
      min-height: 491px;
      padding: 46px 120px 185px;
      width: 1300px;
      margin: 0 auto;
    }
  }

  &__title {
    font-family: "Oswald", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 36px;
    line-height: 36px;
    color: $white;
    padding-bottom: 24px;
    padding-top: 27px;
    max-width: 249px;
    margin: 0 auto;

    @media (min-width: $tablet-width) {
      font-size: 60px;
      line-height: 60px;
      color: $black;
      margin: 0;
      max-width: 420px;
      text-align: left;
      margin-bottom: 41px;
      padding-top: 60px;
      padding-bottom: 0;
    }
  }

  &__text {
    font-family: "Oswald", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 14px;
    text-transform: uppercase;
    color: $white;
    margin-bottom: 29px;

    @media (min-width: $tablet-width) {
      font-size: 20px;
      line-height: 20px;
      color: $black;
      text-align: left;
      margin-bottom: 49px;
    }

    @media (min-width: $desktop-width) {
      margin-bottom: 52px;
    }
  }

  &__button {
    font-family: "Oswald", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    text-transform: uppercase;
    color: $white;
    background-color: $green-basic;
    text-decoration: none;
    display: block;
    padding: 11px 0;
    max-width: 280px;
    margin: 0 auto;

    &:hover {
      background-color: #5eaa2f;
    }

    &:active {
      background-color: #5eaa2f;
      color: rgb(255, 255, 255, 0.3);
    }

    @media (min-width: $tablet-width) {
      margin: 0;
      font-size: 20px;
      line-height: 20px;
      max-width: 244px;
      padding: 17px 0;
    }
  }

  &__image {
    @media (min-width: $tablet-width) {
      position: absolute;
      z-index: -1;
      top: 565px;
      bottom: 0;
      left: 0;
      right: 0;
      width: 708px;
      height: 609px;
      margin: auto;
    }

    @media (min-width: $desktop-width) {
      width: auto;
      height: auto;
      margin: 0;
      left: 59%;
      transform: translateX(-50%);
      top: 26px;
    }
  }

  &__background {
    position: relative;

    &::before {
      @media (min-width: $desktop-width) {
        background-image: url("../image/background/desktop__back.webp");
        content: "";
        position: absolute;
        top: -200px;
        right: 0;
        bottom: 0;
        left: 50%;
        z-index: -2;
        background-color: #63a63b;
        background-position: 0 100%;
        background-repeat: no-repeat;
      }

      @media (min-resolution: 2dppx) {
        background-image: url("../image/background/desktop__back@2x.webp");
        height: 776px;
        background-size: auto 696px;
      }
    }
  }
}
