.catalog {
  &__title {
    font-family: "Oswald", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 36px;
    line-height: 36px;
    color: $black;
    padding-top: 27px;
    padding-bottom: 41px;

    @media (min-width: $tablet-width) {
      font-size: 60px;
      line-height: 60px;
      padding-top: 61px;
    }

    @media (min-width: $desktop-width) {
      padding-top: 72px;
    }
  }

  &__container {
    border-top: 1px solid #d9d9d9;
    border-bottom: 1px solid #d9d9d9;

    @media (min-width: $tablet-width) {
      border: none;
    }
  }

  &__wrapper {
    width: 320px;
    margin: 0 auto;
    padding: 0 20px;

    @media (min-width: $tablet-width) {
      width: 768px;
      padding: 0 30px;
    }

    @media (min-width: $desktop-width) {
      width: 1300px;
      padding: 0 40px;
    }
  }

  &__item {
    list-style-type: none;
    position: relative;

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: calc(50% - 50vw);
      width: 100vw;
      height: 1px;
      background-color: #ebebeb;

      @media (min-width: $tablet-width) {
        content: none;
      }
    }
  }

  &__heading {
    font-family: "Oswald", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    text-transform: uppercase;
    color: $black;
    margin-bottom: 9px;
    text-align: center;

    @media (min-width: $tablet-width) {
      font-size: 20px;
      line-height: 22px;
      padding: 0 50px;
      margin-bottom: 25px;
    }

    @media (min-width: $desktop-width) {
      padding: 0 10px;
    }
  }

  &__text {
    font-family: "Arial", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 16px;
    color: $special_dark-grey;
    margin-bottom: 21px;
    text-align: center;

    @media (min-width: $tablet-width) {
      font-size: 16px;
      line-height: 18px;
      padding: 0 20px;
      margin-bottom: 86px;
    }

    @media (min-width: $desktop-width) {
      padding: 0;
    }
  }

  &__button {
    font-family: "Oswald", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    text-transform: uppercase;
    color: $special_dark-grey;
    background-color: #f2f2f2;
    padding: 10px 0;
    width: 100%;
    display: block;
    text-align: center;

    &:hover {
      background-color: #ebebeb;
    }

    &:active {
      color: rgb(68, 68, 68, 0.3);
    }
  }

  &__more-product {
    padding: 78px 0 25px;
    background-image: url("../image/icons/cross__mobile.svg");
    background-position: center 25px;
    background-repeat: no-repeat;

    @media (min-width: $tablet-width) {
      background-image: url("../image/icons/cross__tablet.svg");
      background-position: center 51px;
      display: flex;
      flex-direction: column;
      margin-top: 123px;
      border: 1px solid #f2f2f2;
      padding: 159px 38px 38px 38px;
    }

    @media (min-width: $desktop-width) {
      margin-top: 124px;
    }
  }

  &__list {
    @media (min-width: $tablet-width) {
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 19px 60px;
    }

    @media (min-width: $desktop-width) {
      grid-template-columns: repeat(4, 1fr);
      gap: 19px 75px;
    }
  }
}
