.process {
  &__title {
    font-family: "Oswald", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 36px;
    line-height: 40px;
    color: $black;
    margin-bottom: 40px;

    @media (min-width: $tablet-width) {
      font-size: 60px;
      line-height: 60px;
      margin-bottom: 72px;
    }
  }

  &__wrapper {
    padding: 0 20px 49px;
    width: 320px;
    margin: 0 auto;

    @media (min-width: $tablet-width) {
      width: 768px;
      padding: 0 30px 8px;
    }

    @media (min-width: $desktop-width) {
      width: 1300px;
      padding: 0 40px 0;
    }
  }

  &__list {
    list-style-type: none;

    @media (min-width: $tablet-width) {
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 36px 60px;
      counter-reset: process;
    }

    @media (min-width: $desktop-width) {
      grid-template-columns: repeat(4, 1fr);
      column-gap: 80px;
    }
  }

  &__item {
    font-family: "Arial", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    color: $special_dark-grey;
    margin-bottom: 20px;
    display: grid;
    grid-template-columns: 60px 1fr;
    column-gap: 20px;
    position: relative;

    br {
      display: none;
    }

    @media (min-width: $tablet-width) {
      font-size: 16px;
      line-height: 24px;
      margin-bottom: 0;
      grid-template-columns: auto;
      padding-right: 72px;
      min-height: 280px;
      grid-template-rows: 80px auto;
      gap: 32px;
      counter-increment: process;

      br {
        display: block;
      }
    }

    @media (min-width: $desktop-width) {
      padding-right: 0;
    }

    &:last-child {
      margin-bottom: 0;
    }

    &::before {
      content: "";
      width: 60px;
      height: 60px;
      background-color: $green-basic;
      background-position: 50% 50%;
      background-repeat: no-repeat;

      @media (min-width: $tablet-width) {
        width: 80px;
        height: 80px;
      }
    }

    &::after {
      @media (min-width: $tablet-width) {
        content: counter(process);
        position: absolute;
        top: -25px;
        right: 72px;
        z-index: -1;
        min-width: 150px;
        color: $basic_grey;
        font-family: "Oswald", "Arial", sans-serif;
        font-size: 280px;
        text-align: center;
        line-height: 1;
      }

      @media (min-width: $desktop-width) {
        top: -36px;
        right: -8px;
      }
    }

    &--leaf::before {
      background-image: url("../image/icons/icon_leaf.svg");
    }

    &--tub::before {
      background-image: url("../image/icons/icon_tub.svg");
    }

    &--eat::before {
      background-image: url("../image/icons/icon_eat.svg");
    }

    &--alarm::before {
      background-image: url("../image/icons/icon_alarm.svg");
    }
  }
}
