.form {
  &__wrapper {
    border-top: 1px solid #d9d9d9;
    padding-top: 27px;

    @media (min-width: $tablet-width) {
      border-top: 0;
      padding-top: 61px;
    }

    @media (min-width: $desktop-width) {
      padding-top: 71px;
    }
  }

  &__title {
    font-family: "Oswald", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 36px;
    line-height: 36px;
    color: $black;
    margin-bottom: 37px;

    @media (min-width: $tablet-width) {
      font-size: 60px;
      line-height: 60px;
      margin-bottom: 54px;
    }
  }

  &__text {
    font-family: "Oswald", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    text-transform: uppercase;
    color: $black;
    margin-bottom: 39px;
    padding-right: 50px;

    @media (min-width: $tablet-width) {
      font-size: 20px;
      line-height: 30px;
      width: 323px;
      padding-right: 0;
      margin-bottom: 64px;
    }
  }

  &__field {
    border: none;

    &--padding {
      padding: 0 20px 26px;

      @media (min-width: $tablet-width) {
        padding: 0 70px 71px;
      }

      @media (min-width: $desktop-width) {
        grid-column: 1/2;
        padding: 0 41px;
      }
    }

    &--radio {
      @media (min-width: $tablet-width) {
        padding: 0 70px;
      }

      @media (min-width: $desktop-width) {
        grid-column: 2/-1;
      }

      @media (min-width: $desktop-width) {
        padding: 0 39px;
      }
    }

    &--contacts {
      padding: 36px 20px 23px;

      @media (min-width: $tablet-width) {
        padding: 73px 70px 64px;
      }

      @media (min-width: $desktop-width) {
        grid-column: 1/-1;
        display: grid;
        grid-template-columns: 1fr 1fr;
        column-gap: 78px;
        padding: 70px 40px 64px;
      }
    }

    &--message {
      padding: 0 20px;

      @media (min-width: $tablet-width) {
        padding: 0 70px;
      }

      @media (min-width: $desktop-width) {
        grid-column: 1/-1;
        padding: 0 40px;
      }
    }

    &--checkbox {
      padding: 0 20px;

      @media (min-width: $tablet-width) {
        padding: 0 70px;
      }

      @media (min-width: $desktop-width) {
        grid-column: 1/-1;
        padding: 0 40px;
      }
    }
  }

  &__label {
    font-family: "Oswald", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 18px;
    text-transform: uppercase;
    color: $special_dark-grey;
    margin-right: 8px;

    @media (min-width: $tablet-width) {
      font-size: 20px;
      line-height: 30px;
      margin-right: 14px;
    }

    &--margin {
      margin-right: 15px;
    }
  }

  &__input {
    padding: 14px 0 13px 14px;
    width: 100%;
    border: 1px solid #e7e7e7;

    @media (min-width: $tablet-width) {
      padding: 20px 0 19px 18px;
    }

    @media (min-width: $desktop-width) {
      padding: 26px 0 14px 20px;
    }

    &:hover {
      border: 1px solid #cdcdcd;
    }

    &:focus {
      outline: 2px solid $special_dark-grey;
    }

    &:valid {
      outline: 2px solid #ff8282;
    }

    &::placeholder {
      font-family: "Oswald", sans-serif;
      font-style: normal;
      font-weight: 400;
      font-size: 16px;
      line-height: 18px;
      text-transform: uppercase;
      color: $special_dark-grey;

      @media (min-width: $tablet-width) {
        font-size: 20px;
        line-height: 30px;
      }
    }
  }

  &__group {
    display: flex;
    align-items: center;
    margin-bottom: 16px;

    &:last-child {
      margin-bottom: 0;
    }

    @media (min-width: $desktop-width) {
      margin-bottom: 0;
    }

    &--margin {
      @media (min-width: $desktop-width) {
        margin-bottom: 16px;
      }
    }
  }

  &__content {
    width: 320px;
    margin: 0 auto;

    @media (min-width: $tablet-width) {
      width: 768px;
    }

    @media (min-width: $desktop-width) {
      width: 1300px;
      display: grid;
      grid-template-columns: 1fr 1fr;
    }
  }

  &__heading {
    width: 320px;
    margin: 0 auto;
    padding: 0 20px;

    @media (min-width: $tablet-width) {
      width: 768px;
      padding: 0 70px;
    }

    @media (min-width: $desktop-width) {
      width: 1300px;
      padding: 0 40px;
    }
  }

  &__radio-name {
    font-family: "Oswald", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 18px;
    text-transform: uppercase;
    color: #222222;
    padding-left: 45px;

    @media (min-width: $tablet-width) {
      font-size: 20px;
      line-height: 30px;
      padding-left: 55px;
    }
  }

  &__item-radio {
    list-style-type: none;
    margin-bottom: 22px;
  }

  &__input-radio[type="radio"] + &__control {
    border-radius: 50%;
  }

  &__input-radio[type="radio"]:checked + &__control::before {
    position: absolute;
    top: 8px;
    left: 8px;
    width: 13px;
    height: 13px;
    background-color: $green-basic;
    border-radius: 50%;
    content: "";

    @media (min-width: $tablet-width) {
      width: 18px;
      height: 18px;
      top: 10px;
      left: 10px;
    }
  }

  &__label-radio {
    position: relative;
    display: block;
  }

  &__control {
    width: 30px;
    height: 30px;
    position: absolute;
    border: 1px solid #cdcdcd;
    border-radius: 50%;
    top: -5px;

    &:hover {
      border: 1px solid #bababa;
    }

    &:active {
      border: 1px solid #bababa;
      opacity: 0.3;
    }

    @media (min-width: $tablet-width) {
      width: 40px;
      height: 40px;
    }
  }

  &__list-radio {
    border: 1px solid #d9d9d9;
    padding: 28px 20px 1px;

    @media (min-width: $tablet-width) {
      padding: 28px 32px 9px;
    }

    @media (min-width: $desktop-width) {
      padding: 33px 32px 9px;
    }
  }

  &__title-field {
    font-family: "Oswald", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 26px;
    color: $black;

    @media (min-width: $tablet-width) {
      font-size: 36px;
      line-height: 36px;
    }

    &--contacts-title {
      padding-top: 23px;

      @media (min-width: $tablet-width) {
        padding-top: 75px;
      }

      @media (min-width: $desktop-width) {
        padding-top: 67px;
      }
    }

    &--contacts-title::before {
      content: "";
      position: absolute;
      right: 0;
      left: 0;
      height: 2px;
      margin-top: 16px;
      background-color: $green-basic;

      @media (min-width: $tablet-width) {
        margin-top: 22px;
      }
    }

    &--message-title::before {
      content: "";
      position: absolute;
      right: 0;
      left: 0;
      height: 2px;
      margin-top: 16px;
      background-color: $green-basic;

      @media (min-width: $tablet-width) {
        margin-top: 22px;
      }

      @media (min-width: $desktop-width) {
        margin-top: 25px;
      }
    }

    &--checkbox-title::before {
      content: "";
      position: absolute;
      right: 0;
      left: 0;
      height: 2px;
      margin-top: 16px;
      background-color: $green-basic;

      @media (min-width: $tablet-width) {
        margin-top: 22px;
      }
    }

    &--contacts-title span {
      position: relative;
      z-index: 1;
      padding-left: 20px;
      background-color: $white;
      display: block;
      width: 221px;
      padding-right: 20px;
      margin-left: -20px;

      @media (min-width: $tablet-width) {
        margin-left: -44px;
        padding: 0 44px;
        width: auto;
      }

      @media (min-width: $desktop-width) {
        margin-left: -40px;
        padding: 0 40px;
      }
    }

    &--message-title span {
      position: relative;
      z-index: 1;
      padding-left: 20px;
      background-color: $white;
      display: block;
      width: 164px;
      padding-right: 20px;
      margin-left: -20px;

      @media (min-width: $tablet-width) {
        margin-left: -37px;
        padding: 0 35px;
        width: auto;
      }

      @media (min-width: $desktop-width) {
        margin-left: -40px;
        padding: 0 40px;
      }
    }

    &--checkbox-title span {
      position: relative;
      z-index: 1;
      padding-left: 20px;
      background-color: $white;
      display: block;
      width: 184px;
      padding-right: 20px;
      margin-left: -20px;

      @media (min-width: $tablet-width) {
        margin-left: -28px;
        padding: 0 28px;
        width: auto;
      }

      @media (min-width: $desktop-width) {
        margin-left: -40px;
        padding: 0 40px;
      }
    }
  }

  &__input-message {
    margin-top: 38px;
    width: 100%;
    resize: none;
    min-height: 150px;
    padding: 8px 12px 0 15px;
    border: 1px solid #e7e7e7;
    margin-bottom: 19px;

    @media (min-width: $tablet-width) {
      margin-top: 77px;
      margin-bottom: 84px;
    }

    @media (min-width: $desktop-width) {
      min-height: 174px;
      margin-bottom: 65px;
    }

    &::placeholder {
      font-family: "Oswald", sans-serif;
      font-style: normal;
      font-weight: 400;
      font-size: 16px;
      line-height: 18px;
      text-transform: uppercase;
      color: $special_dark-grey;

      @media (min-width: $tablet-width) {
        font-size: 20px;
        line-height: 30px;
      }
    }
  }

  &__list-checkbox {
    list-style-type: none;
    margin-top: 38px;

    @media (min-width: $tablet-width) {
      display: grid;
      grid-template-columns: 1fr 1fr;
      column-gap: 66px;
      row-gap: 19px;
      margin-bottom: 36px;
      margin-top: 62px;
    }

    @media (min-width: $desktop-width) {
      grid-template-columns: repeat(4, 1fr);
      column-gap: 80px;
    }
  }

  &__item-checkbox {
    position: relative;
    display: block;
    margin-bottom: 23px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__input-checkbox[type="checkbox"] + &__control {
    border-radius: 0;
  }

  &__control-name {
    padding-left: 48px;
    font-family: "Oswald", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 18px;
    text-transform: uppercase;
    color: $black;

    @media (min-width: $tablet-width) {
      font-size: 20px;
      line-height: 30px;
      padding-left: 58px;
    }
  }

  &__input-checkbox[type="checkbox"]:checked + &__control {
    background: url("../image/icons/icon__checkbox.svg") no-repeat center;
  }

  &__submit {
    padding: 38px 20px 0;

    @media (min-width: $tablet-width) {
      padding: 0 70px;
      display: grid;
      grid-template-columns: 282px 1fr;
      align-items: center;
      padding-bottom: 73px;
    }

    @media (min-width: $desktop-width) {
      grid-column: 1/-1;
      grid-template-columns: 568px 1fr;
      padding: 10px 40px 98px;
    }
  }

  &__button-submit {
    width: 100%;
    background-color: $green-basic;
    font-family: "Oswald", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    text-transform: uppercase;
    color: $white;
    border: none;
    padding: 10px 0;
    margin-bottom: 21px;

    &:hover {
      background-color: #5eaa2f;
    }

    &:active {
      background-color: #5eaa2f;
      color: rgb(255, 255, 255, 0.3);
    }

    @media (min-width: $tablet-width) {
      margin-bottom: 0;
      font-size: 20px;
      padding: 16px 0;
    }
  }

  &__text-required {
    text-align: center;
    font-family: "Arial", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 16px;
    color: $black;
    padding-bottom: 32px;

    @media (min-width: $tablet-width) {
      text-align: left;
      padding-left: 61px;
      font-size: 16px;
      line-height: 16px;
      padding-bottom: 0;
    }

    @media (min-width: $desktop-width) {
      padding-left: 82px;
    }
  }

  &__input[type="email"] {
    background-image: url("../image/icons/icon_email.svg");
    background-repeat: no-repeat;
    background-position: center right 13px;

    &:focus {
      background-image: url("../image/icons/icon__email.svg");
      background-position: center right 13px;
      background-size: 20px 15px;
    }

    &:valid {
      background-image: url("../image/icons/icon-email__error.svg");
    }
  }

  &__input[type="tel"] {
    background-image: url("../image/icons/icon__phone.svg");
    background-repeat: no-repeat;
    background-position: center right 13px;

    &:focus {
      background-image: url("../image/icons/icon-phone__focus.svg");
      background-position: center right 13px;
    }

    &:valid {
      background-image: url("../image/icons/icon-phone__error.svg");
    }
  }
}
