.footer {
  &__icon-VK {
    width: 23px;
    height: 16px;
    fill: #666666;

    &:hover {
      fill: $green-basic;
    }

    &:active {
      fill: $green-basic;
      opacity: 0.6;
    }

    @media (min-width: $tablet-width) {
      width: 30px;
      height: 22px;
    }
  }

  &__icon-instagram {
    width: 16px;
    height: 16px;
    fill: #666666;

    &:hover {
      fill: $green-basic;
    }

    &:active {
      fill: $green-basic;
      opacity: 0.6;
    }

    @media (min-width: $tablet-width) {
      width: 23px;
      height: 23px;
    }
  }

  &__icon-facebook {
    width: 22px;
    height: 22px;
    stroke: #666666;

    &:hover {
      stroke: $green-basic;
    }

    &:active {
      stroke: $green-basic;
      opacity: 0.6;
    }

    @media (min-width: $tablet-width) {
      width: 28px;
      height: 33px;
    }
  }

  &__icon-copyright {
    width: 27px;
    height: 32px;
    fill: #666666;

    @media (min-width: $tablet-width) {
      width: 27px;
      height: 34px;
    }
  }

  &__wrapper {
    width: 320px;
    margin: 0 auto;
    padding: 40px 20px 22px;

    @media (min-width: $tablet-width) {
      width: 768px;
      padding: 58px 30px 55px;
      display: flex;
      align-items: center;
      justify-content: space-between;
    }

    @media (min-width: $desktop-width) {
      width: 1300px;
      padding: 76px 40px 58px;
    }
  }

  &__container {
    background-color: $basic_grey;
  }

  &__list {
    list-style-type: none;
    display: flex;
    align-items: center;
    justify-content: center;
    border-top: 1px solid #d9d9d9;
    border-bottom: 1px solid #d9d9d9;
    padding: 17px 0;
    margin-bottom: 23px;

    @media (min-width: $tablet-width) {
      border: none;
      padding: 0;
      margin-bottom: 0;
      display: grid;
      grid-template-columns: repeat(3, 30px);
      column-gap: 33px;
    }
  }

  &__logo-image {
    display: block;
    margin: 0 auto;
    margin-bottom: 32px;

    &:hover {
      opacity: 0.8;
    }

    &:active {
      opacity: 0.6;
    }

    @media (min-width: $tablet-width) {
      margin-bottom: 0;
    }
  }

  &__item {
    margin-right: 36px;

    &:last-child {
      margin-right: 0;
    }

    &--VK {
      @media (min-width: $tablet-width) {
        margin-left: 22px;
      }
    }

    &--inst {
      @media (min-width: $tablet-width) {
        margin-left: 19px;
      }
    }
  }

  &__copyright-link {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-family: "Arial", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 18px;
    color: $special_dark-grey;
    text-decoration: none;

    @media (min-width: $tablet-width) {
      display: grid;
      grid-template-columns: 1fr min-content;
      column-gap: 22px;
    }
  }

  &__copyright-link:hover &__icon-copyright {
    fill: $green-basic;
  }

  &__copyright-link:active &__icon-copyright {
    fill: $green-basic;
    opacity: 0.6;
  }
}
