.example {
  &__title {
    font-family: "Oswald", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 36px;
    line-height: 40px;
    color: $black;
    margin-bottom: 40px;

    @media (min-width: $tablet-width) {
      font-size: 60px;
      line-height: 60px;
      margin-top: 39px;
      margin-bottom: 65px;
    }

    @media (min-width: $desktop-width) {
      grid-column: 1/-1;
      margin-bottom: 147px;
      margin-top: 30px;
    }
  }

  &__text {
    font-family: "Arial", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    color: $special_dark-grey;
    margin-bottom: 20px;

    @media (min-width: $tablet-width) {
      font-size: 16px;
      line-height: 24px;
      grid-column: 1/-1;
      padding-right: 10px;
      margin-bottom: 53px;
    }

    @media (min-width: $desktop-width) {
      padding-right: 190px;
      margin-bottom: 69px;
    }
  }

  &__wrapper {
    padding: 25px 20px 44px;
    width: 320px;
    margin: 0 auto;

    @media (min-width: $tablet-width) {
      width: 768px;
      padding: 3px 30px 62px;
    }

    @media (min-width: $desktop-width) {
      width: 1300px;
      padding: 0 40px 0;
      display: flex;
    }
  }

  &__container {
    background-color: $special_grey;

    @media (min-width: $tablet-width) {
      border-top: 1px solid #e6e6e6;
      background-image: linear-gradient(to top, #ebebeb 521px, $white 490px);
    }

    @media (min-width: $desktop-width) {
      border-top: none;
    }
  }

  &__list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 32px;
    margin-bottom: 35px;

    @media (min-width: $tablet-width) {
      min-width: 371px;
    }

    @media (min-width: $desktop-width) {
      margin-bottom: 49px;
    }
  }

  &__list-title {
    font-family: "Arial", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 12px;
    color: $special_dark-grey;
    text-transform: lowercase;
    text-align: center;
    margin-bottom: -26px;
    width: 80px;
    background-color: #ebebeb;
    padding: 0 4px;

    @media (min-width: $tablet-width) {
      font-size: 14px;
      line-height: 14px;
      width: auto;
      padding: 0 6px;
      margin-bottom: -21px;
      background-color: $white;
    }

    @media (min-width: $desktop-width) {
      background-color: #ebebeb;
    }
  }

  &__description {
    font-family: "Oswald", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 24px;
    color: $black;
    text-align: center;
    margin-bottom: 7px;
    text-transform: uppercase;

    @media (min-width: $tablet-width) {
      font-size: 30px;
      line-height: 37px;
    }
  }

  &__item {
    border: 1px solid #cdcdcd;
    border-radius: 3px;
    padding: 12px;
    display: flex;
    align-items: center;
    flex-direction: column-reverse;

    @media (min-width: $tablet-width) {
      padding: 17px 10px 16px 10px;
    }

    @media (min-width: $desktop-width) {
      align-self: start;
    }
  }

  &__spending {
    text-align: center;
    font-family: "Oswald", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: $black;
    margin-bottom: 40px;
    text-transform: uppercase;

    @media (min-width: $tablet-width) {
      font-size: 20px;
      line-height: 30px;
      text-transform: uppercase;
      padding-left: 90px;
      text-align: right;
      padding-top: 5px;
    }

    @media (min-width: $desktop-width) {
      grid-column: 1/-1;
      padding-left: 0;
      text-align: left;
      display: grid;
      grid-template-columns: 237px min-content;
    }
  }

  &__button {
    font-family: "Oswald", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    text-transform: uppercase;
    color: $black;
    background-color: transparent;
    border: none;

    @media (min-width: $tablet-width) {
      font-size: 20px;
      line-height: 30px;
      border-bottom: 1px dotted rgba(0, 0, 0, 0.24);

      &:hover {
        border-bottom: 1px solid rgba(0, 0, 0, 0.4);
      }

      &:active {
        border-bottom: 1px solid rgba(0, 0, 0, 0);
      }
    }
  }

  &__toggler {
    padding: 3px 6px;
    display: flex;
    background-color: $white;
    border: 1px solid #cdcdcd;
    border-radius: 6px;
    justify-content: center;

    @media (min-width: $tablet-width) {
      display: none;
    }

    &::after {
      content: "";
      width: 36px;
      height: 5px;
      background-color: $green-basic;
      border-radius: 6px;
    }
  }

  &__switcher--before &__toggler {
    justify-content: flex-start;
  }

  &__switcher--after &__toggler {
    justify-content: flex-end;
  }

  &__switcher {
    display: grid;
    grid-template-columns: 49px 1fr 49px;
    align-items: center;
    gap: 13px;
    padding: 16px 35px 0;

    @media (min-width: $tablet-width) {
      padding: 29px 69px 0;
    }

    @media (min-width: $desktop-width) {
      padding: 29px 23px 0;
    }
  }

  &__image {
    display: none;

    &--active {
      display: block;
    }
  }

  &__content {
    @media (min-width: $tablet-width) {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      padding-bottom: 49px;
    }

    @media (min-width: $desktop-width) {
      grid-template-columns: 366px 70px 1fr;
    }
  }

  &__picture {
    @media (min-width: $tablet-width) {
      display: block;
      margin: 0 auto;
    }

    @media (min-width: $desktop-width) {
      margin-top: -12px;
    }
  }

  &__range {
    display: none;

    @media (min-width: $tablet-width) {
      appearance: none;
      background-color: transparent;
      display: block;
    }
  }

  &__range::-moz-range-thumb {
    height: 30px;
    width: 30px;
    padding: 0;
    border: 11px solid $white;
    border-radius: 50%;
    background-color: #cdcdcd;
    box-shadow:
      0 0 0 1px #cdcdcd,
      0 1px 3px rgba($black, 0.16);
    cursor: pointer;

    &:hover {
      box-shadow:
        0 0 0 1px #cdcdcd,
        0 2px 4px rgba($black, 0.19);
    }

    &:active {
      box-shadow:
        0 0 0 1px $green-basic,
        0 3px 5px rgba($black, 0.2);
    }
  }

  &__range::-webkit-slider-thumb {
    appearance: none;
    height: 30px;
    width: 30px;
    margin-top: -12px;
    padding: 0;
    border: 11px solid $white;
    border-radius: 50%;
    background-color: $green-basic;
    box-shadow:
      0 0 0 1px #cdcdcd,
      0 1px 3px rgba($black, 0.16);
    cursor: pointer;

    &:hover {
      box-shadow:
        0 0 0 1px #cdcdcd,
        0 2px 4px rgba($black, 0.19);
    }

    &:active {
      box-shadow:
        0 0 0 1px $green-basic,
        0 3px 5px rgba($black, 0.2);
    }
  }

  &__range::-webkit-slider-runnable-track,
  &__range::-moz-range-track {
    height: 8px;
    background-color: #dcdcdc;
    border-radius: 6px;
  }

  &__range::-webkit-slider-runnable-track {
    height: 8px;
    background-color: #dcdcdc;
    border-radius: 6px;
  }
}
