.map {
  &__text {
    font-family: "Oswald", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    text-transform: uppercase;
    color: #111111;

    @media (min-width: $tablet-width) {
      font-size: 20px;
      line-height: 26px;
    }
  }

  &__address {
    font-family: "Arial", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: $special_dark-grey;
    text-align: left;

    @media (min-width: $tablet-width) {
      text-align: right;
      font-size: 16px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
    }

    @media (min-width: $desktop-width) {
      text-align: left;
    }
  }

  &__wrapper {
    width: 320px;
    margin: 0 auto;

    @media (min-width: $tablet-width) {
      width: 768px;
    }

    @media (min-width: $desktop-width) {
      width: 1300px;
      position: relative;
    }
  }

  &__contacts {
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding: 26px 20px;

    @media (min-width: $tablet-width) {
      padding: 59px 70px;
      grid-template-columns: 172px 172px;
      justify-content: space-between;
    }

    @media (min-width: $desktop-width) {
      position: absolute;
      z-index: 1;
      background-color: $white;
      top: 78px;
      padding: 57px 62px 61px 79px;
      column-gap: 79px;
      left: 41px;
    }

    &--background {
      background-color: #f2f2f2;
    }
  }

  &__picture {
    display: block;

    @media (min-width: $desktop-width) {
      width: 100%;
    }
  }
}
