.additional-items {
  &__title {
    font-family: "Oswald", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 24px;
    color: $black;
    margin-bottom: 36px;
    margin-top: 23px;
    background-color: $white;
    padding: 0 22px;
    margin-left: -22px;
    justify-self: start;

    @media (min-width: $tablet-width) {
      font-size: 36px;
      line-height: 36px;
      padding: 0 38px;
      margin-left: -1px;
      margin-top: 67px;
      margin-bottom: 80px;
    }

    @media (min-width: $desktop-width) {
      margin-top: 75px;
      padding: 0 39px;
      margin-left: -39px;
      margin-bottom: 81px;
    }

    &::before {
      content: "";
      position: absolute;
      top: 38px;
      left: calc(50% - 50vw);
      width: 100vw;
      height: 2px;
      background-color: $green-basic;
      z-index: -1;

      @media (min-width: $tablet-width) {
        top: 89px;
      }

      @media (min-width: $desktop-width) {
        top: 98px;
      }
    }
  }

  &__wrapper {
    width: 320px;
    padding: 0 20px;
    margin: 0 auto;
    position: relative;
    display: grid;
    padding-bottom: 35px;

    @media (min-width: $tablet-width) {
      width: 768px;
      padding: 0 30px 76px;
    }

    @media (min-width: $desktop-width) {
      width: 1300px;
      padding: 0 40px 104px;
      grid-template-columns: 896px 1fr;
      column-gap: 79px;
    }
  }

  &__list {
    list-style-type: none;

    @media (min-width: $desktop-width) {
      grid-column: 1/2;
    }
  }

  &__heading {
    font-family: "Oswald", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 16px;
    text-transform: uppercase;
    color: #222222;
    margin-bottom: 13px;

    @media (min-width: $tablet-width) {
      font-size: 20px;
      line-height: 20px;
    }

    @media (min-width: $desktop-width) {
      margin-bottom: 0;
    }
  }

  &__description-definition {
    font-family: "Arial", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 14px;
    color: $special_dark-grey;

    @media (min-width: $tablet-width) {
      font-size: 16px;
      line-height: 20px;
      text-align: right;
    }

    @media (min-width: $desktop-width) {
      &--price {
        text-align: end;
      }

      text-align: start;
    }
  }

  &__button {
    font-family: "Oswald", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    text-align: center;
    text-transform: uppercase;
    color: $white;
    background-color: $green-basic;
    padding: 10px 0;
    text-decoration: none;
    width: 100%;
    display: block;

    &:hover {
      background-color: #5eaa2f;
    }

    &:active {
      background-color: #5eaa2f;
      color: rgb(255, 255, 255, 0.3);
    }

    @media (min-width: $tablet-width) {
      grid-column: 1/-1;
    }

    @media (min-width: $desktop-width) {
      grid-column: 5/-1;
      justify-self: flex-end;
    }
  }

  &__item {
    border-top: 1px solid #cdcdcd;
    padding: 13px 0 19px;

    &:last-child {
      border-bottom: 1px solid #cdcdcd;
    }

    @media (min-width: $tablet-width) {
      border: 1px solid #cdcdcd;
      border-bottom: none;
      padding: 26px 38px 26px;
      display: grid;
      grid-template-columns: repeat(2, 1fr);
    }

    @media (min-width: $desktop-width) {
      grid-template-columns: 1fr 38px 1fr 38px 244px;
      align-items: center;
      border: none;
      border-top: 1px solid #cdcdcd;
      padding: 14px 0 17px 0;
    }
  }

  &__description-list {
    margin-bottom: 14px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    @media (min-width: $tablet-width) {
      display: grid;
      grid-template-columns: 1fr 82px;
      column-gap: 11px;
      margin-bottom: 23px;
      padding-top: 3px;
    }

    @media (min-width: $desktop-width) {
      margin-bottom: 0;
      grid-column: 3/4;
    }
  }

  &__text {
    font-family: "Arial", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    text-align: center;
    color: $white;

    @media (min-width: $tablet-width) {
      text-align: left;
    }

    @media (min-width: $desktop-width) {
      text-align: center;
    }
  }

  &__promo {
    background-image: url("../image/icons/gift__mobile.svg"), url("../image/background/promo-back__mobile.webp");
    background-repeat: no-repeat;
    padding: 186px 61px 43px 58px;
    background-position: 50% 58px, 50% 50%;
    margin-top: 34px;

    @media (min-width: $tablet-width) {
      padding: 70px 120px 70px 422px;
      background-position: 77px 50%, 50% 50%;
      background-image: url("../image/icons/gift__tablet.svg"), url("../image/background/promo-back__tablet.webp");
      margin-top: 69px;
    }

    @media (min-width: $desktop-width) {
      padding: 186px 40px 42px;
      background-position: 50% 58px, 50% 50%;
      margin-top: 0;
      background-image: url("../image/icons/gift__tablet.svg"), url("../image/background/promo-back__mobile.webp");
    }
  }
}
